import React, {useState} from 'react';
import {makeStyles} from '@material-ui/core/styles'
import { Paper, Grid, Container, InputLabel, MenuItem, TextField, Typography, FormControl, Select, Button } from '@material-ui/core';
import AveryHero from './AveryHero';
import GeneralForm from './forms/GeneralForm'


const useStyles = makeStyles(theme => ({
    root: {

    },
    mainPaper: {
        marginTop: '5vh',
        textAlign: 'center'
    },
    contactHeader: {
        fontSize: '1.2rem',
        fontFamily: 'K2D'
    },
    form: {
        minWidth: 300
    },
    text: {
        paddingBottom: '20px',
        minWidth: 250
    }
}));

export default function Contact() {
    
    function BookingForm() {
        return (
            <Typography>Booking</Typography>
        )
    }

    function WebForm() {
        return (
            <Typography>Website</Typography>
        )
    }


    const classes = useStyles();

    const [selection, setSelection] = useState("");

    const handleSelectionChange = event => {
        setSelection(event.target.value);
    }
    return (
        <div className={classes.root}>
            <AveryHero
                primary="Contact Us"
                secondary="Get in touch with us, or check out our social media!"
            />
                <Grid
                className={classes.main}
                container
                spacing={0}
                alignItems="left"
                justify="center"
                >
                    <Grid item md={4}>
                        <Paper className={classes.mainPaper}>
                            <Typography className={classes.contactHeader}>
                                Contact Form
                            </Typography>
                            <hr/>
                            <FormControl className={classes.form}>
                                <InputLabel id="form-select-label">Select Option</InputLabel>
                                <Select
                                labelId="form-select-label"
                                id="form-select"
                                value={selection}
                                onChange={handleSelectionChange}
                                >
                                    <MenuItem value="general">General Enquiries</MenuItem>
                                    <MenuItem value="booking">Booking Enquiries</MenuItem>
                                    <MenuItem value="web">Website enquiries</MenuItem>
                                </Select>
                            </FormControl>
                            {selection === "general" &&
                                <GeneralForm/>
                            }
                            {selection === "booking" &&
                                <BookingForm/>
                            }
                            {selection === "web" &&
                                <WebForm/>
                            }
                        </Paper>
                        <br/>
                    </Grid>
                </Grid>
        </div>
    )
}