import React from 'react';
import {makeStyles} from '@material-ui/core/styles'
import { Paper, Grid, Typography, CardMedia, Card, CardContent} from '@material-ui/core';
import AveryHero from './AveryHero'
import PamperForm from './forms/PamperForm'
import Vie from './static/Pamper/vie.jpg'
import Sky from './static/Pamper/sky.jpg'


const useStyles = makeStyles(theme => ({
    root: {

    },
    pamperText: {
        fontFamily: 'Rubik',
        fontSize: '1.2rem',
        textAlign: 'center',
        margin: '30px'
    },
    card: {
        boxShadow: '0 6px 12px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)'
    },
    cardMedia: {
    },
    cardCaption : {
        fontSize: '1.1rem',
        textAlign: 'center',
        fontFamily: 'Ubuntu',
    },
    formHeading: {
        fontSize: '1.4rem',
        textAlign: 'center',
        fontFamily: 'Ubuntu'
    }
}))

function Pamper() {
    const classes = useStyles();
    return (
        <div>
            <AveryHero
            primary="Aston's Pamper Project"
            secondary="Treat a deserving person to a few hours of luxury."
            />
            <Grid 
            container
            spacing={0}
            alignItems="center"
            justify="center"
            >
                <Grid item xs={10}>
                    <Paper data-aos="fade-up">
                        <Grid
                        container
                        spacing={3}
                        alignItems="center"
                        justify="center"
                        >
                            <Grid item md={5}>
                                <Card className={classes.card}>
                                    <CardMedia
                                    className={classes.cardMedia}
                                    component="img"
                                    alt="The Vie Aesthetics Salon"
                                    height="400"
                                    image={Vie}
                                    title="The Vie Aesthetics Salon"
                                    /> 
                                    <CardContent>
                                        <Typography className={classes.cardCaption} variant="h6" color="textSecondary">
                                            The Vie Aesthetics salon.
                                        </Typography>
                                    </CardContent>
                                </Card> 
                            </Grid>
                            <Grid item md={5}>
                                <Card className={classes.card}>
                                    <CardMedia
                                    className={classes.cardMedia}
                                    component="img"
                                    alt="Sky, a past pamper project winner"
                                    height="400"
                                    image={Sky}
                                    title="Sky, a past winner"
                                    />
                                    <CardContent>
                                        <Typography className={classes.cardCaption} variant="h6" color="textSecondary">
                                            Sky, a past winner, at her pampering session.
                                        </Typography>
                                    </CardContent>
                                </Card>
                            </Grid>
                        </Grid>
                        <Typography className={classes.pamperText}>
                            Born from Aston's idea to reach out to other local charities and deserving people, Aston's Pamper Project is a venture designed to give
                            back to the community. He hopes for it to grow and assist as many people as possible.
                            The selected nominees will be treated to a luxury pamper day thanks to the team's fundraising, held at <a href="http://www.vie-aesthetics.com/" target="_blank" rel="noopener noreferrer">Vie Aesthetics</a>,
                            a high end salon based in Rayleigh. <strong>Nominees must be over 18.</strong> Please fill out the form and nominate that deserving person in your life!
                        </Typography>
                        <hr/>
                        <Typography className={classes.formHeading}>
                            Nominate that special someone in your life by submitting this form! Please note that hypertext links (http/https) are not permitted. This is to prevent spam.
                        </Typography>
                        <PamperForm/>
                    </Paper>
                </Grid>
            </Grid>
        </div>
    )
}
export default Pamper;