import React, {useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid'
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";

const useStyles = makeStyles(theme =>({
    heroContainer: {
        marginTop: '15px'
    },
    heroTitle: {
        fontSize: '2rem',
        fontFamily: 'K2D, sans-serif',
        color: 'white',
    },
    heroText: {
        fontSize: '1.2rem',
        fontFamily: 'Ubuntu, sans-serif',
        color: 'white',
    },
    button: {
        backgroundColor: '#209cee',
        color: 'white',
        textDecoration: 'none'
    },
}))

function AveryHero() {

    const classes=useStyles()
    return (
        <Container maxWidth="sm" className={classes.heroContainer}>
        <Typography className={classes.heroTitle} align="center" color="textPrimary" gutterBottom>
            Team Avery Essex
        </Typography>
        <Typography className={classes.heroText} align="center" color="textSecondary" paragraph>
            Empowering diversity across Essex through meaningful interactions and events with the community.
        </Typography>
        <div className={classes.heroButtons}>
            <Grid container spacing={2} justify="center">
                <Grid item>
                <Link to="/events">
                    <Button className={classes.button} variant="contained" color="primary">Events</Button>
                </Link>
                </Grid>
                <Grid item>
                    <Link to="/team">
                        <Button className={classes.button} variant="contained">Meet the Team</Button>
                    </Link>
                </Grid>
            </Grid>
        </div>
        </Container>
    )
}

export default AveryHero;