import React from 'react';
import {makeStyles} from '@material-ui/core/styles'
import { Paper, Grid, Typography, CardMedia, Card, CardContent} from '@material-ui/core';
import AveryHero from './AveryHero'
import PamperForm from './forms/PamperForm'
import Presentation from './static/ThisIsMe/presentation.jfif'
import Chrissy from './static/ThisIsMe/chrissy.jfif'


const useStyles = makeStyles(theme => ({
    root: {

    },
    pamperText: {
        fontFamily: 'Rubik',
        fontSize: '1.2rem',
        textAlign: 'center',
        margin: '30px'
    },
    card: {
        boxShadow: '0 6px 12px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)'
    },
    cardMedia: {
    },
    cardCaption : {
        fontSize: '1.1rem',
        textAlign: 'center',
        fontFamily: 'Ubuntu',
    },
    formHeading: {
        fontSize: '1.4rem',
        textAlign: 'center',
        fontFamily: 'Ubuntu'
    }
}))

function ThisIsMe() {
    const classes = useStyles();
    return (
        <div>
            <AveryHero
            primary="This is Me"
            secondary="Learn about Aston's incredible journey through his new short film!"
            />
            <Grid 
            container
            spacing={0}
            alignItems="center"
            justify="center"
            >
                <Grid item xs={10}>
                    <Paper data-aos="fade-up">
                        <Grid
                        container
                        spacing={3}
                        alignItems="center"
                        justify="center"
                        >
                            <Grid item md={5}>
                                <Card className={classes.card}>
                                    <CardMedia
                                    className={classes.cardMedia}
                                    component="img"
                                    alt="Aston presenting This is Me at a local school!"
                                    height="400"
                                    image={Presentation}
                                    title="Aston presenting This is Me at a local school!"
                                    /> 
                                    <CardContent>
                                        <Typography className={classes.cardCaption} variant="h6" color="textSecondary">
                                            Aston presenting "This is Me" at a local school.
                                        </Typography>
                                    </CardContent>
                                </Card> 
                            </Grid>
                            <Grid item md={5}>
                                <Card className={classes.card}>
                                    <CardMedia
                                    className={classes.cardMedia}
                                    component="img"
                                    alt="Sky, a past pamper project winner"
                                    height="400"
                                    image={Chrissy}
                                    title="Sky, a past winner"
                                    />
                                    <CardContent>
                                        <Typography className={classes.cardCaption} variant="h6" color="textSecondary">
                                            Aston before appearing on the <a href="https://youtu.be/zh2Wj-4Da3Q" rel="noreferrer noopener">Chrissy B Show.</a>
                                        </Typography>
                                    </CardContent>
                                </Card>
                            </Grid>

                        </Grid>
                        <Typography className={classes.pamperText}>
                            
                        </Typography>
                        <hr/>
                        <Typography className={classes.formHeading}>
                            Nominate that special someone in your life by submitting this form! Please note that hypertext links (http/https) are not permitted. This is to prevent spam.
                        </Typography>
                    </Paper>
                </Grid>
            </Grid>
        </div>
    )
}
export default ThisIsMe;