import React, {useState} from 'react';
import {Container, TextField, CircularProgress, Button} from '@material-ui/core';


function GeneralForm() {
    const [name, setName] = useState("")
    const [email, setEmail] = useState("")
    const [subject, setSubject] = useState("")
    const [text, setText] = useState("")
    const [isLoading, setIsLoading] = useState(false)

    function handleSubmit() {
        setIsLoading(true)
    }


    const LinkCheck = /https?:\/\/(www\.)?[-a-zA-Z0-9@:%._\+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_\+.~#?&//=]*)/
    const EmailCheck = /^[a-zA-Z0-9]+@(?:[a-zA-Z0-9]+\.)+[A-Za-z]+$/
    if (isLoading) {
        return <Container><br/><CircularProgress size={150} thickness={1.5}/><br/></Container>
    }
    else {
        return (
            <Container>
                <TextField
                variant="outlined"
                margin="normal"
                required
                fullWidth
                id="general-name"
                label="Your Name"
                name="general-name"
                error={LinkCheck.test(name)}
                value={name}
                onChange={e => setName(e.target.value)}
                />
                <br/>
                <TextField
                variant="outlined"
                margin="normal"
                required
                fullWidth
                id="general-email"
                label="Your E-Mail Address"
                name="general-email"
                autoComplete="email"
                error={!(EmailCheck.test(email))}
                helperText="Please enter a valid e-mail address."
                value={email}
                onChange={e => setEmail(e.target.value)}
                />
                <TextField
                variant="outlined"
                margin="normal"
                required
                fullWidth
                id="general-subject"
                label="Subject"
                name="general-subject"
                error={LinkCheck.test(subject)}
                value={subject}
                onChange={e => setSubject(e.target.value)}
                />
                <br/>
                <TextField
                variant="outlined"
                required
                fullWidth
                id="general-text"
                label="Enter message"
                placeholder="Your message here..."
                multiline
                rows={4}
                margin="normal"
                error={LinkCheck.test(text)}
                value={text}
                onChange={e => setText(e.target.value)}
                />
                <br/>
                <Button onClick={handleSubmit}>Submit</Button>
            </Container>
        )
    }
}
export default GeneralForm;